<template>
	<view class="container wx-container">
		<view class="input-box">
			<input type="text" :value="searchVal" focus @input="inputVal" placeholder="查找商品 如:苹果" class="input" @confirm="searchGoods()">
			<image src="../../static/icon/search.png" class="icon" @click="searchGoods()">
		</view>
		<view class="history-box">
			<view class="title-box">
				<view class="title">历史记录</view>
				<view class="icon-box" @click="delHistory">
					<uni-icons type="trash" size="24" color="#909399"></uni-icons>
				</view>
			</view>
			<view class="history" v-if="history.length>0">
				<view class="item" v-for="(item,index) in history" :key="index" @click="searchGoods(item)" v-if="isShowHistory==false?index<10:index<history.length">{{item}}</view>
				<view class="item" v-if="history.length>10"><uni-icons type="bottom" size="10" v-if="!isShowHistory" @click="setShowHistory"></uni-icons></view>
			</view>
			<view class="no" v-else>无历史记录</view>
		</view>
		<view class="goods-list wx-goods-list">
			<view class="title">搜索商品</view>
			<view class="goods-box" v-if="goodsList.length>0">
				<view class="goods-item" v-for="(item,index) in goodsList" :key="item.id" @click="toDetail(item.id)">
					<view class="l">
						<image :src="item.image" class="img"></image>
						<view class="info">
							<view class="name line">{{item.title}}</view>
							<view class="dec line2">{{item.describe}}</view>
							<view class="price">￥{{item.price}}/份</view>
						</view>
					</view>
					<view class="r" @click.stop="addCartGoods(item.id)">
						<image src="../../static/icon/gouwuche.png" class="icon"></image>
					</view>
				</view>		
			</view>
			<view class="no" v-else>无商品</view>
		</view>
	</view>
</template>

<script>
	import {mapState,mapMutations,mapActions} from 'vuex'
	export default{
		data(){
			return{
				searchVal:'',
				goodsList:[],
				isShowHistory:false
			}
		},
		computed:{
			...mapState(['history'])
		},
		methods:{
			...mapMutations(['setHistory']),
			...mapActions(['addCartGoods']),
			inputVal(e){
				this.searchVal = e.detail.value
			},
			// 搜索商品
			async searchGoods(name) {
				if(name){
					this.searchVal = name
				}
				if(this.searchVal!==''){
					let searchVal = this.searchVal
					// 添加历史记录
					let history = this.history==''?[]:this.history
					let status = true
					if(history.length>0){
						history.forEach(item => {
							if(item==searchVal){
								status=false
							}
						})
						if(status){
							history.push(searchVal)
						}
					}else{
						history.push(searchVal)
					}
					this.setHistory(history)
					// 获取商品
					const response = await this.$api.goods.searchGoods(searchVal)
					if (response.data.code === 200) {
						this.goodsList = response.data.data
					}else{
						this.goodsList=[]
					}
				}else{
					this.goodsList=[]
				}
			},
			// 跳转商品详情
			toDetail(id){
				uni.navigateTo({
					url:'/pages/goods/detail?goodsId='+id
				})
			},
			// 删除历史记录
			delHistory(){
				var that = this
				uni.showModal({
					title: '',
					content: '确定删除全部搜索历史？',
					confirmColor:'#009000',
					success: function (res) {
						if (res.confirm) {
							that.setHistory([])
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			setShowHistory(){
				this.isShowHistory = !this.isShowHistory
			}
		}
	}
</script>

<style lang="scss">
	.container{
		min-height: calc(100vh - 44px);
		background-color: #fff;
		.title{
			font-size: 28upx;
			font-weight: bold;
		}
		.no{
			padding: 20upx;
			font-size: 24upx;
			color: #c7c7c7;
		}
		.input-box{
			width: 100%;
			padding: 10upx 20upx;
			position: relative;
			.input{
				width: 100%;
				height: 65upx;
				line-height: 65upx;
				background-color: #efefef;
				padding: 0 90upx 0 30upx;
				border-radius: 50upx;
				font-size: 28upx;
			}
			.icon{
				width: 85upx;
				height:65upx;
				padding: 10upx 18upx 15upx 28upx;
				position: absolute;
				top: 10upx;
				right: 20upx;
			}
		}
		.history-box{
	        .title-box{
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 20upx 10upx 0 20upx;
				.icon-box{
					padding: 10upx;
				}
			}
			.no{
				padding-top:10upx;
			}
			.history{
				padding: 15upx 20upx 0;
				overflow: hidden;
				.item{
					font-size: 26upx;
					float: left;
					padding: 0 20upx;
					background-color: #efefef;
					border-radius: 50upx;
					margin: 0 20upx 25upx 0;
				}
			}
		}
		.goods-list{
			.title{
				padding: 20upx 20upx 0;
			}
			.goods-box{
				.goods-item{
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: space-between;
					background-color: #fff;
					border-radius: 8upx;
					padding: 40upx;
					margin-bottom: 10upx;
					border-bottom: 1px solid #F0F0F0;
					&:last-child{
						border: 0;
					}
					.l{
						display: flex;
						justify-content: flex-start;
						.img{
							width: 200upx;
							height: 200upx;
							border-radius: 12upx;
						}
						.info{
							margin-left: 20upx;
							width: 320upx;
							.name{
								font-size:30upx;
								font-weight: bold;
								color: $uni-main-color;
								height: 50upx;
								line-height: 50upx;
							}
							.dec{
								font-size: 22upx;
								height: 72upx;
								line-height: 36upx;
								color: $uni-secondary-color;
								margin: 15upx 0 25upx;
							}
							.price{
								font-size: 26upx;
								font-weight: bold;
								color: #ff557f;
								font-family: '雅黑';
							}
						}
					}
					.r{
						.icon{
							width: 50upx;
							height: 50upx;
						}
					}
				}
			}
		}
	}
	/* #ifdef MP-WEIXIN */
	.wx-container{
		min-height: 100vh;
	}
	/* #endif */
</style>